import React from 'react'; import styled, { css } from 'styled-components'; 

const Content = (props) => (
    <ContentContainer aboutme>
        {props.children}
    </ContentContainer>
)

export const ContentContainer = styled.div `
    width: 98%; min-height: 93vh; height: 93%; margin: 4px auto; overflow: auto; border-radius: 60px 125px 60px 60px; font-family: Caveat;
    box-shadow: 0 0 8px 0px #000; border: 8px solid rgba(255,255,255,.5); z-index: 1;
    ${props => props.aboutme && css`
    background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82)`};
    `
export default Content